.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);

  padding: 93px 15px;
  overflow-y: scroll;

  opacity: 1;
  transition: opacity var(--transition-duration) var(--transition-timing-function);

  &--is-hidden {
    opacity: 0;
    pointer-events: none;
  }
}